import http from "../utils/http";

export function getIp() {
    return http.get('https://2023.ip138.com');
  }
  export function getIp2() {
    return http.get('https://api.iplocation.net/?cmd=get-ip');
  }
  export function getIpInfo(ip) {
    return http.get('https://api.iplocation.net/?ip=' + ip);
  }