<template>
  <div class="app-content" :class="$isMobile() ? 'app-mobile' : 'app-pc'" v-if="isShow">
    <router-view v-slot="{ Component }">
      <keep-alive>
        <component :is="Component"  v-if="$route.meta.keepAlive"/>
      </keep-alive>
      <component :is="Component"  v-if="!$route.meta.keepAlive"/>
    </router-view>
  </div>
   <div class="air-app-box" v-else>
      {{$t('index.備註')}}
    </div>
</template>

<script>
import web3Data from "@/utils/initWeb3";
import { toRefs, reactive, onMounted } from 'vue'
import { getIp, getIp2, getIpInfo } from "@/api"

export default {
  name: 'App',
  setup () {
    const data = reactive({
      isShow: true,
      getIpInfo: async () => {
        let countryNames = ['China', 'Russia', 'Korea', 'India', 'Mexico', 'Syria', 'Tunisia', 'Serbia']

        let ip = await getIp2()
        let ipInfo = await getIpInfo(ip.ip);
        countryNames.forEach((item) => {
          if(ipInfo.country_name.includes(item)) {
            data.isShow = false
          }
        })
      }
    })
          // 禁止ios10 以上页面缩放
    web3Data.initWeb3()
    document.documentElement.addEventListener('touchstart', function (event) {
      if (event.touches.length > 1) {
        event.preventDefault()
      }
    }, false)
    let lastTouchEnd = 0
    document.addEventListener('touchend', function (event) {
      let now = Date.now()
      if (now - lastTouchEnd <= 300) {
        event.preventDefault()
      }
      lastTouchEnd = now
    }, false)
    // 阻止双指放大
    document.addEventListener('gesturestart', function (event) {
      event.preventDefault()
    })
    onMounted(() => {
      let key = ['俄罗斯', '中国', '朝鲜', '印度', '也门', '墨西哥', '叙利亚', '突尼斯', '塞尔维亚']
      // let key = ['俄罗斯',  '朝鲜', '印度', '也门', '墨西哥', '叙利亚', '突尼斯', '塞尔维亚']
      getIp().then((res) => {
        key.forEach((item) => {
          if(res.includes(item)) {
            data.isShow = false
          }
        })
      }).catch(() => {
        data.getIpInfo()
      })
    })
     const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="less">
@import "./assets/style/font.less";
@import './assets/style/common.less';
.app-content {
  margin: 0 auto;
  padding: 0 0 1px;
}
.app-mobile{
  background: url('~@/assets/images/bg.png') no-repeat center top #07071C;
  background-size: 100%;
  max-width: 640px;
  margin: 0 auto;
}
.app-pc{
   min-height: 1000px;
   background:
    url('~@/assets/images/pc/bg1.png'),
    url('~@/assets/images/pc/bg2.png'),
    url('~@/assets/images/pc/bg3.png'),
    url('~@/assets/images/pc/bg4.png') #0B0B22; 
     background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
    background-position: center 0, center 1499px, center 2998px, center 4497px;
}
</style>

<style lang="less" scoped>
.air-app-box{
  color: #FFD154;
  font-size: 18px;
  line-height: 30px;
  padding: 40px 20px;
}
</style>
